/* stylelint-disable */
.slot {
  padding: 50px 0;
  background: #691A2E url("../img/home/slot/bg-desc.webp") no-repeat 50% 0/cover;
}
.slot__container {
  display: flex;
  flex-direction: column;
}
.slot__title {
  margin: 0 0 10px 0;
  color: #FFFFFF;
  text-align: center;
}
.slot__text {
  font-size: 15px;
  line-height: 22px;
  color: #808080;
  text-align: center;
  max-width: 940px;
  margin: 0 auto 20px auto;
}
.slot__link {
  font-family: "Popins-semb", sans-serif;
  font-size: 17px;
  line-height: 20px;
  letter-spacing: 0.02em;
  color: #FFFFFF;
  align-self: center;
  text-align: center;
  padding: 19px 85px 17px;
}
.slot__list {
  display: grid;
  grid-template-columns: auto auto auto auto;
  gap: 25px;
  margin: 0 0 30px 0;
}
.slot__item {
  display: flex;
  flex-direction: column;
  padding: 20px;
  border-radius: 12px;
  background: #FFFFFF;
  position: relative;
  max-width: 330px;
}
.slot__item-info {
  position: absolute;
  cursor: pointer;
  top: 20px;
  right: 20px;
  padding: 10.5px;
}
.slot__item-info-text {
  display: none;
  padding: 15px;
  border-radius: 8px;
  background: #691A2E;
  color: #FFFFFF;
  width: 150px;
  position: absolute;
  top: 50px;
  right: 0;
  font-size: 12px;
  line-height: 15px;
}
.slot__item-info-text._show {
  display: block;
}
.slot__item-image {
  max-width: 160px;
  margin: 0 0 10px 0;
}
.slot__item-image img {
  border-radius: 8px;
}
.slot__item-title {
  flex: 1 1 auto;
  font-size: 17px;
  line-height: 26px;
  letter-spacing: -0.004em;
  color: #111111;
  margin: 0 0 2px 0;
}
.slot__item-licensed {
  display: flex;
  color: #2275D4;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.004em;
  margin: 0 0 20px 0;
}
.slot__item-licensed-one {
  margin: 0 8px 0 0;
}
.slot__item-link {
  align-self: flex-start;
  display: inline-block;
  min-width: 160px;
  text-align: center;
  padding: 12px 30px 10px;
  font-size: 15px;
  line-height: 20px;
}

@media screen and (max-width: 1240px) {
  .slot__container {
    padding: 0;
  }
  .slot__title, .slot__text {
    padding: 0 15px;
  }
  .slot__list {
    display: flex;
    overflow-x: scroll;
    align-items: flex-start;
    gap: 0;
    padding: 0 15px;
  }
  .slot__item {
    min-width: 310px;
    max-width: 330px;
  }
  .slot__item:not(:last-child) {
    margin: 0 15px 0 0;
  }
}
@media screen and (max-width: 600px) {
  .slot {
    background: linear-gradient(252.32deg, #8A2336 0%, #631728 28.99%, #340918 100%);
  }
  .slot__text {
    font-size: 13px;
    line-height: 22px;
  }
  .slot__link {
    width: calc(100% - 30px);
  }
}
.faq {
  padding: 55px 0;
  background: #FFFFFF url("../img/general-block-image/bg-faq-desc.webp") no-repeat 50% 0;
  background-size: 100%;
}
.faq__title {
  text-align: center;
  color: #111111;
  margin: 0 0 25px 0;
}

.accordion__item:not(:last-child) {
  margin: 0 0 10px 0;
}

.accordion__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Popins-semb", sans-serif;
  padding: 12px 25px 10px 25px;
  border-radius: 8px;
  background: #FCEDF0;
  font-size: 20px;
  line-height: 48px;
  text-transform: uppercase;
  color: #111111;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.accordion__header::after {
  display: inline-block;
  margin: 0 0 0 25px;
  width: 38px;
  height: 38px;
  min-width: 38px;
  min-height: 38px;
  border-radius: 8px;
  background: #691A2E url("../img/general-block-image/polygon-faq.svg") no-repeat center;
  background-size: 11px 7px;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  content: "";
}

.accordion__item_slidedown .accordion__header,
.accordion__item_show .accordion__header {
  border: none;
}

.accordion__header:hover {
  background-color: #dec2c8;
}

.accordion__content {
  display: flex;
  flex-direction: column;
  padding: 25px 25px 15px 25px;
  background: transparent;
}
.accordion__content p {
  font-size: 15px;
  line-height: 30px;
  color: #111111;
}
.accordion__content p:not(:last-child) {
  margin: 0 0 20px 0;
}

.accordion__item:not(.accordion__item_show) .accordion__body {
  display: none;
}

@media screen and (max-width: 600px) {
  .faq {
    padding: 50px 0 30px 0;
    background: #FFFFFF;
  }
  .faq__container {
    padding: 0;
  }
  .faq__title {
    margin: 0 0 20px 0;
    padding: 0 15px;
  }
  .accordion__header {
    border-radius: 0;
    padding: 19px 14px 17px;
    font-size: 14px;
    line-height: 21px;
  }
  .accordion__content {
    padding: 15px 15px 10px 15px;
  }
  .accordion__content p {
    font-size: 12px;
    line-height: 26px;
  }
  .accordion__content p:not(:last-child) {
    margin: 0 0 15px 0;
  }
}
.author {
