@media screen and (max-width: 374px) {
  .author__image {
    max-width: 55px;
    min-width: 55px;
    margin: 0 15px 0 0;
  }
  .author__image img {
    min-width: 55px;
  }
}
.subscribe {
  padding: 70px 0 90px 0;
  background: #691A2E url("../img/general-block-image/subscribe/bg-sub-desc.webp") no-repeat 50% 0/cover;
}
.subscribe__row {
  max-width: 580px;
}
.subscribe__title {
  color: #FFFFFF;
  margin: 0 0 10px 0;
}
.subscribe__text {
  font-size: 15px;
  line-height: 22px;
  color: #808080;
  margin: 0 0 30px 0;
}
.subscribe__form {
  margin: 0 0 5px 0;
}
.subscribe__form-wrap {
  max-width: 460px;
  position: relative;
}
.subscribe__email {
  width: 100%;
  border-radius: 8px;
  background: #FFFFFF;
  padding: 10px 19px 8px;
  font-family: "Popins-reg", sans-serif;
  font-size: 15px;
  line-height: 30px;
}
.subscribe__email::placeholder {
  font-size: 15px;
  line-height: 30px;
  color: #111111;
}
.subscribe__submit {
  padding: 12px 25px 10px;
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
}
.subscribe__info {
  max-width: 490px;
  color: #FFFFFF;
  font-size: 13px;
  line-height: 17px;
}
.subscribe__info-link {
  font-family: "Popins-semb", sans-serif;
  color: #FFFFFF;
  white-space: nowrap;
  text-decoration: underline;
}

@media screen and (max-width: 600px) {
  .subscribe {
    padding: 50px 0 200px 0;
    background: #4a101d url("../img/general-block-image/subscribe/bg-sub-mob.webp") no-repeat 50% 100%/100%;
  }
  .subscribe__title {
    text-align: center;
    margin: 0 0 10px 0;
  }
  .subscribe__text {
    text-align: center;
    font-size: 13px;
    line-height: 22px;
    margin: 0 0 20px 0;
  }
  .subscribe__form-wrap {
    max-width: 100%;
    display: flex;
    flex-direction: column;
  }
  .subscribe__email {
    margin: 0 0 15px 0;
    display: inline-block;
  }
  .subscribe__submit {
    display: inline-block;
    position: static;
    align-self: center;
    transform: translateY(0);
  }
  .subscribe__info {
    text-align: center;
  }
}
.footer {
  padding: 50px 0 30px 0;
  background: #111111;
}
.footer__link {
  display: inline-block;
  max-width: 240px;
  margin: 0 0 45px 0;
}
.footer__wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin: 0 0 60px 0;
}
.footer__row {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0 60px 0 0;
}
.footer__item {
  display: flex;
  flex-direction: column;
  max-width: 270px;
}
.footer__item:not(:last-child) {
  margin: 0 60px 0 0;
}
.footer__item-title {
  font-family: "Popins-semb", sans-serif;
  color: #FFFFFF;
  font-size: 17px;
  line-height: 22px;
  letter-spacing: 0.04em;
  margin: 0 0 20px 0;
}
.footer__item-link {
  display: inline-block;
  font-size: 15px;
  line-height: 20px;
  color: #C1C1D4;
  transition: color 0.3s ease;
}
.footer__item-link:hover {
  color: #FFFFFF;
}
.footer__item-link:not(:last-child) {
  margin: 0 0 20px 0;
}
.footer__social {
  display: grid;
  grid-template-columns: auto auto auto;
  gap: 10px 15px;
}
.footer__social-item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 64px;
  height: 42px;
  border-radius: 8px;
}
.footer__social-item svg {
  max-width: 100%;
  max-height: 100%;
  fill: #FFFFFF;
}
.footer__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer__sponsor {
  display: flex;
  align-items: center;
  margin: 0 30px 0 0;
}
.footer__sponsor-item {
  display: inline-block;
}
.footer__sponsor-item:not(:last-child) {
  margin: 0 25px 0 0;
}
.footer__text {
  color: #808080;
  font-size: 13px;
  line-height: 20px;
}
.footer__text-link {
  white-space: nowrap;
  color: #FFFFFF;
  text-decoration: underline;
}
.footer__cookie {
  position: fixed;
  width: 80%;
  padding: 20px 40px;
  background: linear-gradient(252.32deg, #8A2336 0%, #631728 28.99%, #340918 100%);
  z-index: 10;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 30px;
  display: flex;
  align-items: center;
}
.footer__cookie._hidden {
  display: none;
  visibility: hidden;
}
.footer__cookie-img {
  min-width: 40px;
  max-width: 41px;
  margin: 0 15px 0 0;
}
.footer__cookie-info {
  display: flex;
  align-items: center;
  flex: 1 1 auto;
}
.footer__cookie-text {
  font-size: 13px;
  line-height: 16px;
  letter-spacing: 0.01em;
  color: #FFFFFF;
  flex: 1 1 auto;
  margin: 0 20px 0 0;
}
.footer__cookie-btn {
  display: inline-block;
  padding: 12px 25px 10px;
  white-space: nowrap;
}
.footer__btn-top {
  position: fixed;
  bottom: 40px;
  right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}
.footer__btn-top._hidden {
  opacity: 0;
  visibility: hidden;
}

@media screen and (max-width: 1600px) {
  .footer__cookie {
    width: 70%;
  }
}
@media screen and (max-width: 1170px) {
  .footer__row {
    display: grid;
    grid-template-columns: auto auto auto;
    gap: 30px;
    margin: 0 30px 0 0;
  }
  .footer__info {
    flex-direction: column;
    align-items: flex-start;
  }
  .footer__sponsor {
    margin: 0 0 25px 0;
  }
  .footer__item:not(:last-child) {
    margin: 0;
  }
}
@media screen and (max-width: 750px) {
  .footer__row {
    display: grid;
    grid-template-columns: auto auto;
    gap: 30px;
  }
  .footer__cookie {
    width: 60%;
  }
}
@media screen and (max-width: 600px) {
  .footer {
    padding: 30px 0;
  }
  .footer__wrapper {
    flex-direction: column;
    align-items: stretch;
    margin: 0 0 40px 0;
  }
  .footer__row {
    margin: 0 0 40px 0;
    grid-template-columns: calc(50% - 15px) calc(50% - 15px);
    gap: 30px 15px;
  }
  .footer__item {
    max-width: 100%;
  }
  .footer__social {
    align-self: center;
  }
  .footer__sponsor {
    justify-content: center;
    margin: 0 0 15px 0;
  }
  .footer__sponsor-item:not(:last-child) {
    margin: 0 15px 0 0;
  }
  .footer__text {
    font-size: 11px;
    line-height: 16px;
  }
  .footer__cookie {
    align-items: flex-start;
    padding: 20px 15px;
    bottom: 10px;
    width: 98%;
  }
  .footer__cookie-text {
    font-size: 13px;
    line-height: 22px;
    margin: 0 0 10px 0;
  }
  .footer__cookie-info {
    flex-direction: column;
    align-items: flex-start;
  }
  .footer__btn-top {
    z-index: 10;
    bottom: 29px;
    right: 20px;
    width: 45px;
    height: 43px;
  }
}